.App {
    font-family: monospace;
    font-size: 16px;
}

.Actiranker .Inputarea {
    margin: 10px;
}

.Inputarea label, input {
    margin: 10px;
}

.Inputarea input[type='radio'] {
    margin-right: 0;
}

.Actiranker table {
    width: 100%;
    border-collapse: collapse;
}

.Actiranker th, td {
    border: 1px solid black;
    text-align: center;
 }

 .Actiranker .left {
     text-align: left;
 }
